.title {
  height: 100px;
  width: 300px;
  margin: 60px auto 0px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-transform: capitalize;
}
.title h1{
  color: rgb(10, 34, 57);
}

.title .horizontal-line {
  width: 100px;
  height: 5px;
  background-color: #5f7bdf;
  border: 1px solid #5f7bdf;
  margin-top: 10px;
}
