.card {
  /* display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 2.5fr 3.5fr 3.5fr 2.5fr; */
  width: 300px;
  height: auto;
  margin: 0px auto;
  border-radius: 8px;
  /* border: 1px solid #E2E5E2; */
  box-shadow: 0px 7px 22px 7px rgba(145, 144, 144, 0.75);
}

.card > div {
  /* border: 1px solid black; */
}
.details {
  background-color: rgb(32, 41, 66);
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  width: 100%;
  height: 97.91px;
  
}
.details h4 {
  color: white;
  letter-spacing: 2px;
  text-transform: capitalize;
  font-weight: 500;
  padding-top: 20px;
}
.details p {
  color: rgb(69, 80, 103);
  letter-spacing: 1px;
  margin-top: 5px;
}

.list {
  width: 100%;
  height: 120px;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 4fr 4fr 4fr;
  padding: 10px 0px;
}

.listElement {
  display: grid;
  grid-template-rows: 1fr;
  grid-template-columns: 2fr 10fr;
}
.listIcon{
  display: flex;
  justify-content: center;
  align-items: center;
}
.listIcon i{
  color: rgb(176, 187, 198);
  font-size: 11px;
}
.listSpecification{
  color:#0A2239;
  font-weight: 600;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.briefing {
  width: 100%;
  /* height: 137.08px; */
  /* display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 9fr 3fr; */
}
.briefing .para {
  display: none;
  color:#0A2239;
  text-align: left;
  font-size: 12px;
  font-weight: 400;
  padding-left: 17px;
  width: 100%;
  height: 102.69px;
  transition: all 2s;
}
.toggle {
  width: 100%;
  height: 34.23px;
  word-spacing: 3px;
  text-transform: capitalize;
  color: #2F5DFF;
  background-color: #d3ddff;
  transition: all 2s;
  display: flex;
  justify-content: center;
  align-items: center;
}
.toggle span.toggle-icon {
  margin-top: 0px;
  color: #194DFF;
}
.personals {
  width: 100%;
  height: 80px;
  display: grid;
  grid-template-columns: 3fr 9fr;
  grid-template-rows: 6fr 6fr;
  grid-template-areas:
    "picture name"
    "picture location";
}

.personals .picture{
  grid-area: picture;
  margin:10px 0px;
}

.personals .name {
  grid-area: name;
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
  color: #0A2239;
  font-weight: 600;
}

.personals .location {
  grid-area: location;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  color: #8693A7;
  font-size: 13px;
}
