*, *::before, *::after {
  box-sizing: border-box;
  margin: 0px;
  padding:0px;
}
.bid{
    width: 300px;
    margin: 38px auto 20px;
}

.bid-price{
    border: 1px solid #E2E5E2;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
    width: 100%;
    height: 225px;

}
.bid-heading{
    height: 54.875px;
    width: 100%;
    color:#0A2239;
    margin-top: 17px;
}

.bid-heading .horizontal-line {
  width: 100px;
  height: 2px;
  background-color: #5f7bdf;
  border: 1px solid #5f7bdf;
  margin: 8px auto 13px;
}

.bid-price-enter{
    width: 100%;
    height: 80px;
    display: grid;
    grid-template-columns: 3fr 9fr;
    grid-template-rows: 1fr;
    color:#0D2D11
}
.bid-price-enter .rupeeSymbol{
    font-size: 50px;
    font-weight: 600;

}

.bid-price-enter input{
    outline:none;
    appearance: none;
    border:none;
    width: 90%;
    font-size: 50px;
    color:#0D2D11;
    padding-top: 10px;
    font-weight: 600;

}
.bid-price-enter input:focus{
    color: rgb(109, 134, 226);
}
.bid-price-enter input::-webkit-outer-spin-button,
.bid-price-enter input::-webkit-inner-spin-button, .inputfield input::-webkit-outer-spin-button,
.inputfield input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/*Firefox */
.bid-price-enter input[type=number], .inputfield input[type=number] {
  -moz-appearance: textfield;
} */
.bid-radio-select{
    height: 46.875px;
    width: 100%;

}
.bid-radio-select{
	background-color: white;
	height: 40px;
	width: 260px;
	position: relative;
}

input[type="radio"]{
	display: none;
}

.label1,.label2{
	height: 40px;
	width: 130px;
    background-color: #FAFCFD;
    position: absolute;
	top:2px;
	transition: 0.5s;
    color: #6A7987;
    border: 1px solid #E7EBEE;
    border-radius: 26px;
    margin-left: 10px;
}
.label1 {
    width: 98px;
    left: 6px;
}
.label2{
	right: -20px;
}

.bid-radio-select span{
	display: flex;
	height: 90px;
	width: 130px;
	justify-content: center;
	align-items: center;
    font-weight: 700;

    position: absolute;
    top: -24px;
    font-size: 15px;
    font-weight: 400;
	
}
.bid-radio-select .label1 span{
    left: -16px;
    top: -26px;
}
.bid-radio-select .label2 span{
    top: -26px;
}

input:checked + label{
    color: #3054D6;
    background-color: #F3F5FD;
    border: 1px solid #3054D6;
	transition: 1s;
}


.bid-details{
  display: none;
    width: 100%;
    height: 225px;
    font-size: 16px;
    border: 1px solid #E2E5E2;
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
}

.bid-details .inputfield{
    margin: 23px auto;
}

.custom-field {
  position: relative;
  font-size: 14px;
  padding-top: 20px;
  margin-bottom: 5px;
}

.custom-field input {
  border: none;
  -webkit-appearance: none;
  -ms-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: #f2f2f2;
  padding: 12px;
  border-radius: 3px;
  width: 250px;
  outline: none;
  font-size: 14px;
}

.custom-field .placeholder {
  position: absolute;
  left: 12px;
  top: calc(50% + 10px);
  transform: translateY(-50%);
  color: #919192;
  transition: 
    top 0.3s ease,
    color 0.3s ease,
    font-size 0.3s ease;
}

.custom-field input.dirty + .placeholder,
.custom-field input:valid + .placeholder,
.custom-field input:focus + .placeholder {
  top: 10px;
  font-size: 10px;
  color: #222;
}

/* ONE */
.custom-field.one input {
  background: none;
  border: 1px solid #EEF1F3;
  transition: border-color 0.3s ease;
}

.custom-field.one input + .placeholder {
  left: 8px;
  padding: 0 5px;
}

.custom-field.one input.dirty,
.custom-field.one input:valid,
.custom-field.one input:focus {
  border-color: #222;
  transition-delay: 0.1s;
}
.custom-field.one input:focus {
  border-color: #3054D6;
  transition-delay: 0.1s;
  color: #3054D6;
}
.custom-field.one input:valid{
    color: rgb(10, 34, 57);
}

.custom-field.one input.dirty + .placeholder,
.custom-field.one input:valid + .placeholder,
.custom-field.one input:focus + .placeholder {
  top: 8px;
  font-size: 10px;
  color: #222;
  background: #fff;
}

.custom-field.one input:focus + .placeholder {
  color: #3054D6;
}
.bid + .button{
  width:300px;
  background-color: #EDF0F7;
  color: #B0C2F8;
  text-transform: capitalize;
  margin: 0px auto 10px;
  height: 45px;
  border-radius: 8px;
  font-size: 18px;
  display: flex;
  justify-content: center;
  align-items: center;
}