*{
  box-sizing: border-box;
}
.heading {
  border: 1px solid #fefefe;
  width: 100%;
  height: 70px;
  margin: 0px auto;
  display: grid;
  grid-template-columns: 4fr 4fr 4fr;
  grid-template-rows: 1fr;
  padding: 8px;
  box-shadow: -1px 10px 5px -6px rgba(145, 144, 144, 0.75);
  background-color: white;
  position: fixed;
  top: 0px;
  z-index: 10;
}
.hamburger{
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 8px 12px;
  width: 100%;
  height: 60px;

}
.hamburger .fa-bars{
  font-size: 30px;
  color: #504C4D;
}

.honda-logo{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 60px;

}
.honda-logo div{
  width: 60px;
  height: 60px;
  background: url('https://lh3.googleusercontent.com/proxy/yPEHF_5Voac_mjBof7nLY0p4IoYqOPpCNG7nQ2utQ2q8UmNWnvryRHtEKFeR8P1U4wJLnkEEmTeUZUSk1s1JCCVqmQ');
  background-size: contain;
  background-repeat: no-repeat;
  background-position: 10% 45%;
  
}
/* .honda-logo img{
  width: 30px;
  height: 60px;
  object-fit: fill;
} */

.picture{
  display: flex;
  align-items: center;
  justify-content: flex-end;
  width: 100%;
  height: 60px;
}
.picture div{
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background:url('https://images.unsplash.com/photo-1507003211169-0a1dd7228f2d?ixid=MXwxMjA3fDB8MHxzZWFyY2h8M3x8aGVhZHNob3R8ZW58MHx8MHw%3D&ixlib=rb-1.2.1&w=1000&q=80');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 0% 0%;
  margin-right: 12px;
}
